// check if database exists and get it
databaseExists("UsersData", async isYes => {
	let tmp;
	if (isYes) {
		 indexedDB.open("UsersData").onsuccess = event => {
			const db = event.target.result;
			const objStore = db.transaction("Guest").objectStore("Guest");
			objStore.getAll().onsuccess = event => {
				tmp = event.target.result[0];
			};
		};
	}
	 await wait(500)
	 if (tmp) userData = tmp
	 updateRenderState([<MainPage key={Math.random() * 100} doAnimation={true} />, <NavicationBar key={Math.random() * 100} />])
	console.log(userData)
});
function databaseExists(dbname, callback) {
	const req = indexedDB.open(dbname);
	let existed = true;
	req.onsuccess = _ => {
		req.result.close();
		if (!existed) {
			indexedDB.deleteDatabase(dbname);
		}
		callback(existed);
	};
	req.onupgradeneeded = _ => {
		existed = false;
	};
};